
* {
  color: white;
}

.product--card--container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 100%;

}

.product--card{  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 100vw;
}

.buttons--div{
  display: flex;
  justify-content:space-evenly;
  align-items: center;
}


.deactivate--button{
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}

.deactivate--button:hover{
  background-color: #ff0000;
}

.activate--button{
  color:  white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
  max-width:50%;
}
.activate--button:hover{
  background-color: #00ff00;
}
