/* SHOW FILTERS */
.show--filters--container{
    color: var(--violet-white);
    font-family: 'Oswald';
    display: flex;
    gap: 1em;
    justify-content: flex-start;
    margin: 1em 0;
    align-items: center;
    width: 60vw;
}
.show--filters{
    border: 2px solid var(--white);
    font-size: .75em;
    border-radius: 5px;
    padding: 0.5em;
    display: flex;
    gap: 0.5em;
    align-items: center;
    color: var(--white);
}

.show--filters--button{
    background-color: var(--white);
    padding: .5em;
    border-radius: 5px;
}.show--filters--button:hover{
    cursor: pointer;
    color: var(--white);
    background-color: var(--black);
    transition: .3s;
}
.filters-error{
    color: var(--red);
}