@import './../../index.css';

header {
    color: #ffffff99;
    align-items: center;
    justify-self: center;
    text-shadow: #ffffff 0px 0px 5px;
    width: 100%;
    
}
.scheduleCards {

    border-radius: 10px;
    padding:0% 8%;
    margin: 10px;
    display: flex;
    position: inherit;
    align-content: center;
    justify-content: space-evenly;
    align-content: flex-start;

}

.info {
    justify-self:center;
    align-self: center;
}

.img {
    width: 100px;
}

.content{
    min-width: 120px;
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    background-color: #ffffff33;
    border-radius: 5%;
    padding: 5px;
    margin: 5px;
    box-shadow: #ffffff22 2px 2px 2px;
}

.display {
    background-color: antiquewhite;
    color: black;
    text-decoration: double;
    border-radius: 3%;
}